.intro{
    display:flex;
    height:77vh;
    margin-top: 6rem;
}

.i-left{
    display:flex;
    position:relative;
    flex:1;
    flex-direction: column;
    gap:1rem;

}

.i-name{
    display:flex;
    flex-direction: column;
}


.i-name>:nth-child(1){
    color:var(--black);
    font-weight:bold;
    font-size:3rem;
}

.i-name>:nth-child(2){
    color:var(--orange);
    
    font-weight: bold;
    font-size:3rem;  
}

.i-name>:nth-child(3){
    font-weight:100;
    font-size:14px;
    color:var(--gray);
    
    margin-top:10px;
    
}

.i-button{
    
     width:8rem;
     height:2rem;
     
}

.i-icons{
    margin-top:3rem;
    display:flex;
    gap:0rem;
    
}

.i-icons>*{
     transform: scale(0.5);
}

.i-icons>*:hover{
    cursor:pointer;
}


.i-right{
    flex:1;
    position:relative;
}

.i-right>*{
    position:absolute;
    z-index:1;
}

.i-right>:nth-child(1){
    transform: scale(0.69);
    left:4%;
    top:-27%;
}

.i-right>:nth-child(2){
    top:-8.6rem;
    transform: scale(0.67);
    left:3rem;
}

.i-right>:nth-child(3){
    transform: scale(1.4);
    left:45%;
    top:8%;
}

.i-right>:nth-child(4){
    transform: scale(0.60);
    left:-31%;
    top:-19%;
    border-radius: 50%;
    padding:0;
}

.shivamPic{
    width:192px;
    height:241px;
    
}

