 .floatingdiv{
    justify-content: space-around;
    display: flex;
    background: white;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    align-items: center;
    padding:0px 28px 0px 0px;
    height:4.5rem;
 }

 .floatingdiv>img{
    transform: scale(0.55);
 }

 .floatingdiv>span{
    font-family: sans-serif;
    font-size: 20px;
    color:black;

 }

 .blur{
    position: absolute;
    width: 22rem;
    height: 14rem;
    border-radius: 50%;
    background: #edd0ff;
    z-index:-9;
    top:-18%;
    left:56%;
    filter:blur(72px);
 }