.toggle{
    display: flex;
    justify-content: space-between;
    /* border: 3px solid var(--orange); */
    border:3px solid skyblue;

    border-radius: 1rem;
    position: relative;
    padding: 2px;
    cursor: pointer;
}
.toggle>*{
    width: 1rem;
    height: 1rem;
    /* color:var(--orange); */
    color:skyblue;
}

.t-button{
    border-radius: 100%;
    /* background: var(--orange); */
    background: gray;
    position: absolute;
}